<template>
  <div class="tenant-list-wrapper">
    <app-header useClickLeft @click-left="toLogin" />
    <div class="tenant-list-content">
      <div class="title">
        <div class="title-main">欢迎回到智慧工程</div>
        <div class="title-tip">请选择想要进入的建设单位</div>
      </div>
      <div class="list">
        <div
          class="tenant-item gradient-background "
          v-for="item in tenantList"
          :key="item.tenantCode"
          @click="selectTenantCode(item)"
        >
          <span class="tenant-item-name">{{ item.tenantName }}</span>
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TenantService from '@services/tenant-service';
import UserService from '@services/user-service';
import LoginService from '@services/login-service';
import { getUserContext } from '@yl-app-jssdk/context';

export default {
  data() {
    return {
      tenantList: [],
    };
  },

  activated() {
    TenantService.getTenantList(false).then(res => {
      this.tenantList = res.tenantList;
    });
  },
  methods: {
    toLogin() {
      return LoginService.logout();
    },

    selectTenantCode(item) {
      // 选择租户后，ticket换token
      // TODO:添加loading
      UserService.ticket2token(item.tenantCode).then(() => {
        const _user = getUserContext();
        return LoginService.initAfterLogin(_user).then(() => {
          // 跳转工作台
          this.$router.replace({
            name: 'workbench',
          });
        });
      });
    },
  },
};
</script>

